<template>
  <v-card>
    <v-card-text>
      <v-row>
        <v-col cols="6">
          <mrc_ess_switch :true-value="1"
                          :false-value="0"
                          v-model.number="list.is_used"
                          :readonly="disabled"
                          style="margin-right:35px"
                          :true-text="$t('label_To_enable_the')"
                          :false-text="$t('label_disable')"></mrc_ess_switch>
        </v-col>
        <v-col cols="6">
          <mrc_ess_svgIcon iconClass="web"
                           style="font-size: 23px;" />
          <mrc_ess_svgIcon iconClass="phone"
                           style="font-size: 23px;" />
          11
        </v-col>
        <v-col cols="6">
          <mrc_ess_multiText :label="$t('label_Template_name')+'*'"
                             :isNational="judegNational('template_name')"
                             :readonly="disabled"
                             :error-messages="$errors('list','template_name',requiredRule.concat(nocharacter))"
                             v-model="list.template_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="6">
          <mrc_ess_multiText :label="$t('label_classification')+'*'"
                             :isNational="judegNational('template_code')"
                             :readonly="disabled"
                             :error-messages="$errors('list','template_name',requiredRule.concat(nocharacter))"
                             v-model="list.template_code">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="6">
          <mrc_ess_prompt v-model="list.module_uuid"
                          :obj='module_reference'
                          :label="$t('label_The_module')+'*'"
                          :errMsg="$errors('list','module_uuid',requiredRule)"
                          chips
                          :rules='false'
                          :disabled='disabled'>
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="6">
          <mrc_ess_prompt v-model="list.function_uuid"
                          :obj='function_reference'
                          :errMsg="$errors('list','function_uuid',requiredRule)"
                          :label="$t('label_function')+'*'"
                          chips
                          :rules='false'
                          :disabled='disabled'>
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="12">
          <mrc_ess_multiText v-model="list.client_type"
                             :readonly="disabled"
                             :isNational="judegNational('label_Client_type')"
                             :label="$t('label_Client_type')"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <mrc_ess_multiText :label="$t('label_describe')"
                             :isNational="judegNational('template_remark')"
                             :readonly="disabled"
                             :error-messages="$errors('list','template_remark',requiredRule)"
                             v-model="list.template_remark">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
    </v-card-text>

    <template v-if="$store.state.uuid">
      <mrc_ess_submitBtn @cancel='cancel'
                         @success='success'
                         v-if="!disabled"></mrc_ess_submitBtn>
    </template>
    <template v-else>
      <mrc_ess_submitBtn @cancel='addcancel'
                         @success='addsuccess'
                         :successText="$t('label_The_next_step')"
                         v-if="!disabled"></mrc_ess_submitBtn>
    </template>

  </v-card>

</template> 

<script>
import { getemailtemplatelist, getdetailemailtemplate, updateemailtemplate } from '@/service/api/emailtemplate'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {

  },
  props: {
    disabled: {
      type: Boolean
    }
  },
  data () {
    return {
      list: {
        is_used: 1
      }
    };
  },
  computed: {
    function_reference () {
      return { prompt_name: 'function_reference', uuid: this.list.module_uuid }
    },
    module_reference () {
      return { prompt_name: 'module_reference', uuid: this.$store.state.currentCompany }
    }
  },
  created () { },
  watch: {},
  mounted () {
    if (this.$store.state.uuid) {
      this.getdetail()
    }
    else {

    }
  },

  methods: {
    async getdetail () {
      const res = await getdetailemailtemplate({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'emailtemplate'
        })
      }).catch(err => { })
    },
    success () {
      //  this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      updateemailtemplate(this.$deletefield(null, this.list)).then(res => {
        if (res.code == 200) {
          this.getdetail()
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
        }
        else {
          this.$store.commit('newsTips', res.msg)
        }
      })
    },
    addcancel () {
      this.$myConfirm({
        content: '确认取消新增'
      }).then(res => {
        this.$router.push({
          name: 'emailtemplate'
        })
      }).catch(err => { })
    },
    addsuccess () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      this.$emit('changee1', 2, this.list)
    }
  },
  validations: {
    list: {
      template_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      template_code: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      module_uuid: {
        required
      },
      function_uuid: {
        required
      },
      isshow: {

      },
      template_remark: {
        required
      },
    }
  }
};
</script>

<style scoped>
.v-stepper__content {
  padding: 0;
}
</style>
