<template>
  <v-stepper v-model="e1">
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1"
                      step="1">{{$t('label_The_general_information')}}</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 2"
                      step="2">{{$t('label_The_template')}}</v-stepper-step>

    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <commoninfor @changee1='changee1'></commoninfor>
      </v-stepper-content>

      <v-stepper-content step="2">
        <emailtemplate @changee1='changee1'
                       @submit="submit"></emailtemplate>
      </v-stepper-content>

    </v-stepper-items>
  </v-stepper>
</template>

<script>
import commoninfor from './modules/commoninfor'
import emailtemplate from './modules/emailtemplate'
import { addemailtemplate } from '@/service/api/emailtemplate'

export default {
  components: {
    commoninfor,
    emailtemplate
  },
  data () {
    return {
      e1: 1,
      addlist: {}, //一般资料
      addlist2: {}//模板
    }
  },
  mounted () {

  },
  methods: {
    changee1 (num, val1) {
      this.e1 = num
      val1 ? this.addlist = val1 : ''
    },
    submit (val) {
      // console.log(this.addlist, val);
      this.addlist = { function_uuid: "002", module_uuid: "002", is_used: 1, ...this.addlist }
      addemailtemplate({ ...this.addlist, ...val }).then(res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_added_successfully'))
          this.$router.push({
            name: 'emailtemplate'
          })
        } else {
          this.$store.commit('newsTips', res.msg)
        }
      })
    }
  }
}
</script>